<template>
  <div class="top-menu-container">
    <!-- 桌面端顶部菜单 -->
    <div class="desktop-menu" v-show="!isMobile">
      <div class="menu-content">
        <div class="menu-left">
          <div class="logo-container">
            <img src="/img/logo.png" alt="Logo" class="logo-image">
          </div>
          <div class="menu-items">
            <div 
              v-for="(item, index) in menuItems" 
              :key="index"
              class="menu-item"
              @click="navigateTo(item.url)"
            >
              <span>{{ item.name }}</span>
            </div>
          </div>
        </div>
        <div class="menu-right">
          <div class="login-button" @click="goToLogin">
            <i class="fas fa-sign-in-alt"></i>
            <span>登录</span>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 移动端顶部栏 -->
    <div class="mobile-top-bar" v-show="isMobile">
      <div class="menu-content">
        <div class="menu-toggle" @click="toggleSidebar">
          <i :class="sidebarOpen ? 'fas fa-times' : 'fas fa-bars'"></i>
        </div>
        <div class="mobile-login" @click="goToLogin">
          <i class="fas fa-sign-in-alt"></i>
          <span>登录</span>
        </div>
      </div>
    </div>
    
    <!-- 移动端侧边栏 -->
    <div class="mobile-sidebar" :class="{ 'open': sidebarOpen }" v-show="isMobile">
      <div class="sidebar-logo">
        <img src="/img/logo.png" alt="Logo" class="logo-image">
      </div>
      <div class="sidebar-menu">
        <div 
          v-for="(item, index) in menuItems" 
          :key="index"
          class="sidebar-menu-item"
          @click="navigateTo(item.url); closeSidebar()"
        >
          <span>{{ item.name }}</span>
        </div>
      </div>
    </div>
    
    <!-- 遮罩层 -->
    <div class="sidebar-overlay" v-if="sidebarOpen" @click="closeSidebar"></div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue';

// 菜单项数据
const menuItems = ref([]);
const loading = ref(false);

// 响应式状态
const isMobile = ref(false);
const sidebarOpen = ref(false);

// 加载菜单配置
const loadMenuConfig = async () => {
  if (loading.value) return;
  
  loading.value = true;
  try {
    // 支持http和https，获取当前协议
    const protocol = window.location.protocol;
    const host = window.location.host;
    const menuUrl = `${protocol}//${host}/api/data/menu.json`;
    
    const response = await fetch(menuUrl);
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    
    const data = await response.json();
    menuItems.value = Array.isArray(data) ? data : [];
  } catch (error) {
    console.error('加载菜单配置失败:', error);
    // 使用默认菜单作为降级
    menuItems.value = [
      { name: "首页", url: "/" },
      { name: "文档中心", url: "https://docs.ykccloud.com" },
      { name: "开源仓库", url: "https://github.com/ykccloud" },
      { name: "用户社区", url: "https://community.ykccloud.com" }
    ];
  } finally {
    loading.value = false;
  }
};

// 预加载图片函数
const preloadImages = () => {
  const imagesToPreload = [
    '/img/logo.png'
  ];
  
  imagesToPreload.forEach(src => {
    const img = new Image();
    img.src = src;
  });
};

// 检测窗口大小变化
const checkScreenSize = () => {
  isMobile.value = window.innerWidth < 768;
  if (!isMobile.value) {
    sidebarOpen.value = false;
  }
};

// 切换侧边栏
const toggleSidebar = () => {
  sidebarOpen.value = !sidebarOpen.value;
  if (sidebarOpen.value) {
    document.body.style.overflow = 'hidden'; // 防止背景滚动
  } else {
    document.body.style.overflow = '';
  }
};

// 关闭侧边栏
const closeSidebar = () => {
  sidebarOpen.value = false;
  document.body.style.overflow = '';
};

// 导航到指定URL
const navigateTo = (url) => {
  if (url.startsWith('http://') || url.startsWith('https://')) {
    // 外部链接，在新窗口打开
    window.open(url, '_blank');
  } else {
    // 内部路由，直接跳转
    window.location.href = url;
  }
};

// 跳转到登录页面
const goToLogin = () => {
  window.location.href = '/login';
};

// 生命周期钩子
onMounted(() => {
  // 预加载图片，减少闪烁
  preloadImages();
  
  // 加载菜单配置
  loadMenuConfig();
  
  checkScreenSize();
  window.addEventListener('resize', checkScreenSize);
});

onUnmounted(() => {
  window.removeEventListener('resize', checkScreenSize);
});
</script>

<style scoped>
@import './top.css';

/* 登录按钮样式 */
.login-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 14px;
  font-weight: 500;
}

.login-button:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4190 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.mobile-login {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 12px;
  font-weight: 500;
}

.mobile-login:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4190 100%);
}

/* 移除菜单项的图标样式 */
.menu-item i,
.sidebar-menu-item i {
  display: none;
}

.menu-item {
  cursor: pointer;
}

.sidebar-menu-item {
  cursor: pointer;
}
</style>
